.react-cv-builder.main {
  margin: 0px;
  color: #444;
  scroll-behavior: smooth;
}

.react-cv-builder .page-header {
  background-repeat: no-repeat;
  background-size: cover !important;
  background-position: center !important;
}

.react-cv-builder .profile-intro{
  display: inline-block;
  margin: 0 0 0 30px;
  vertical-align: top;
}
.react-cv-builder .profile-intro .headline{
  letter-spacing: 15px;
}
.react-cv-builder .profile-intro h1{
  font-size: 2.2rem;
  font-weight: 200;
  text-transform: uppercase;
}
.react-cv-builder .profile-intro .profile-detail{
  text-transform: uppercase;
}
.react-cv-builder .profile-intro .small {
  letter-spacing: 8px;
  text-transform: none;
}
.react-cv-builder .profile-intro .headline div {
  margin-bottom: 10px;
}
.react-cv-builder .profile-intro .links span {
  margin: 0 30px 10px 0;
  font-size: 11px;
}
.react-cv-builder .profile-intro .links span svg {
  margin-right: 5px;
}

.react-cv-builder .section-header-desc{
  color: #777;
}

.react-cv-builder .section-header{
  margin: 30px 20px 0;
  text-align: center;
  width: 100%;
}
.react-cv-builder .section-header-desc{
  font-size: 11px;
  margin-top: 5px;
}
.react-cv-builder .oneline{
  overflow: hidden;
  white-space: nowrap;
  word-wrap: break-word;
  text-overflow: ellipsis;
}

.react-cv-builder .vertical-timeline-element {
  margin-bottom: 0px !important;
  margin-top: 0px !important;
}

.react-cv-builder .role-tile .vertical-timeline-element-content {
  box-shadow: 0 0.1rem 1rem 0 rgba(25, 25, 25, 0.1);
  padding: 20px 30px 0px;
}
.react-cv-builder .role-tile .timeline-item-duration{
  display: block;
  margin-bottom: .5rem;
  text-align: right;
  float:right;
  margin-bottom: 0;
}

.react-cv-builder .card-box {
  border-radius: 20px;
  width: 100%;
  padding: 20px 30px;
  box-shadow: 0 0.1rem 1rem 0 rgba(25, 25, 25, 0.1);
  background: #fff;
  font-size: 11px;
}
.react-cv-builder .card-box .details p {
  margin-bottom: 0.5rem;
}

.react-cv-builder .vertical-timeline-element-content,
.react-cv-builder .vertical-timeline-element-content p {
  font-size: 11px;
  font-weight: normal;
  margin-bottom: 16px;
  line-height: 16.5px;
}
.react-cv-builder .vertical-timeline {
  padding: 0px;
}

.react-cv-builder .printonly {
  display: none;
}

.react-cv-builder .role-tile,
.react-cv-builder .card-box {
  page-break-inside: avoid;
}

.react-cv-builder h6 {
  font-size: 11px;
}

@media print {
  .col-print-1 {width:8%; max-width:8%;  float:left;}
  .col-print-2 {width:16%; max-width:16%; float:left;}
  .col-print-3 {width:25%; max-width:25%; float:left;}
  .col-print-4 {width:33%; max-width:33%; float:left;}
  .col-print-5 {width:42%; max-width:42%; float:left;}
  .col-print-6 {width:50%; max-width:50%; float:left;}
  .col-print-7 {width:58%; max-width:58%; float:left;}
  .col-print-8 {width:66%; max-width:66%; float:left;}
  .col-print-9 {width:75%; max-width:75%; float:left;}
  .col-print-10{width:83%; max-width:83%; float:left;}
  .col-print-11{width:92%; max-width:92%; float:left;}
  .col-print-12{width:100%; max-width:100%; float:left;}
  .badge{transition: none !important; box-shadow: none !important; border: none !important;}
}